$background-color_1: #ccc;
$background-color_2: white;
$background-color_3: #2196f3;
$box-shaow-color_1: #2196f3;

.switch {
	position: relative;
	display: inline-block;
	width: 60px;
	height: 34px;
	input {
		opacity: 0;
		width: 0;
		height: 0;
	}
}
.slider {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: $background-color_1;
	transition: 0.4s;
	border-radius: 34px;
	&:before {
		position: absolute;
		content: "";
		height: 26px;
		width: 26px;
		left: 4px;
		bottom: 4px;
		background-color: $background-color_2;
		transition: 0.4s;
		border-radius: 50%;
	}
}
input {
	&:checked {
		+ {
			.slider {
				background-color: $background-color_3;
				&:before {
					transform: translateX(26px);
				}
			}
		}
	}
	&:focus {
		+ {
			.slider {
				box-shadow: 0 0 1px $box-shaow-color_1;
			}
		}
	}
}